<template>
  <ValidationObserver ref="observer">
    <form>
      <ValidationProvider
        v-slot="{ errors }"
        name="Position Type"
        rules="required|max:255"
      >
        <v-text-field
          v-model="data.position"
          :counter="255"
          :error-messages="errors"
          label="Position Type"
          show-hint
          hint="Please indicate if this is an internship position"
          required
        ></v-text-field>
      </ValidationProvider>
      <ValidationProvider
        v-slot="{ errors }"
        name="Project/Company Name"
        rules="required|max:255"
      >
        <v-text-field
          v-model="data.title"
          :counter="255"
          :error-messages="errors"
          label="Project/Company Name"
          required
        ></v-text-field>
      </ValidationProvider>

      <ValidationProvider v-slot="{ errors }" name="category" rules="required">
        <v-select
          v-model="data.category_id"
          :items="categories"
          :error-messages="errors"
          label="Category"
          data-vv-name="category"
          required
          item-text="name"
          item-value="id"
          :loading="categoryLoading"
        >
        </v-select>
      </ValidationProvider>
      <v-slider
        v-model="data.time_line"
        label="Duration of the project/task"
        min="1"
        max="90"
        thumb-label
        required
      >
        <template v-slot:append> {{ data.time_line + ' Days' }} </template>
      </v-slider>
      <date-picker
        v-model="closed_at"
        label="Application  Deadline"
      ></date-picker>
      <v-row>
        <v-col cols="auto">
          <v-switch
            v-model="data.is_free"
            class="mx-2"
            :label="data.is_free ? 'Unpaid' : 'Paid'"
            :readonly="isEdit"
            :disabled="isEdit"
          ></v-switch>
        </v-col>
        <v-col cols="auto">
          <ValidationProvider
            v-if="!data.is_free"
            v-slot="{ errors }"
            name="Cost"
            rules="required|cost:10"
          >
            <!--v-range-slider
              thumb-label="always"
              v-model="data.costs"
              label="Reward"
              step="10"
              max="1000"
              class="mt-5"
            ></v-range-slider-->
            <v-text-field
              v-model="data.cost_min"
              label="Reward"
              prepend-inner-icon="mdi-currency-usd"
              suffix=".00"
              :error-messages="errors"
              :readonly="isEdit"
              :disabled="isEdit"
            ></v-text-field>
          </ValidationProvider>
        </v-col>
      </v-row>
      <v-alert v-if="!data.is_free" text>
        The Project fee is the amount each student will receive after completing
        your task. Your money will be safely deposited on our site and the
        transaction will be held. Don’t worry, if you are unable to hire a
        student on our platform, or if you are dissatisfied with the service,
        you can always request a refund. Email service@beeznests.com
      </v-alert>
      <v-switch
        v-model="data.is_online"
        class="mx-2"
        :label="data.is_online ? 'Remote' : 'Onsite'"
      ></v-switch>
      <v-sheet class="my-2">
        <label>Project description:</label>
      </v-sheet>
      <froala v-model="data.content"></froala>
      <v-sheet class="my-2" v-if="contentError">
        {{ contentError }}
      </v-sheet>
      <v-sheet class="py-6">
        <label>What you will do:</label>
      </v-sheet>
      <froala v-model="data.who"></froala>

      <v-sheet class="py-6">
        <label>Skills and experience you possess:</label>
      </v-sheet>
      <froala v-model="data.skills"></froala>

      <v-sheet class="py-6">
        <label>Who you'll work with:</label>
      </v-sheet>
      <froala v-model="data.work_with"></froala>
      <v-sheet class="mt-3">
        <v-btn class="mr-4" @click="submit" color="primary" depressed
          >next</v-btn
        >
      </v-sheet>
    </form>
  </ValidationObserver>
</template>
<script>
// import { create } from '@/api/project'
import { required, email, max, min_value } from 'vee-validate/dist/rules'
import {
  extend,
  ValidationObserver,
  ValidationProvider,
  setInteractionMode,
} from 'vee-validate'
import { fetchAll } from '@/api/category'

setInteractionMode('eager')

extend('required', {
  ...required,
  message: '{_field_} can not be empty',
})

extend('max', {
  ...max,
  message: '{_field_} may not be greater than {length} characters',
})

extend('min_value', {
  ...min_value,
  message: '{_field_} must {length} aaa',
})

extend('cost', (value) => {
  if (value > 0 && value <= 99999) {
    return true
  }
  return 'The cost of the project must be more than 10$'
})

extend('email', {
  ...email,
  message: 'Email must be valid',
})
import DatePicker from '@/components/DatePicker'
// import Editor from '@/components/Editor'
import Froala from '@/components/Froala'
import moment from 'moment'
export default {
  components: {
    ValidationProvider,
    ValidationObserver,
    DatePicker,
    Froala,
  },
  props: {
    isEdit: {
      type: Boolean,
      default: false,
    },
    data: {
      type: Object,
      default() {
        return {
          title: '',
          position: '',
          cost_min: 0,
          is_free: true,
          is_online: true,
          category_id: null,
          closed_at: moment().add(1, 'months').format('YYYY-MM-DD'),
          content: '',
          who: '',
          work_with: '',
          skills: '',
          time_line: '',
        }
      },
    },
  },
  data: () => ({
    categories: [],
    categoryLoading: false,
    contentError: false,
  }),
  computed: {
    closed_at: {
      get() {
        return moment.utc(this.data.closed_at).format('YYYY-MM-DD')
      },
      set(date) {
        this.data.closed_at = date
      },
    },
  },
  mounted() {
    this.$set(this.data, 'costs', [this.data.cost_min, this.data.cost_max])
    // this.closed_at = this.data.closed_at
    this.loadCategories()
  },
  methods: {
    async loadCategories() {
      try {
        this.categoryLoading = 'info'
        const { data } = await fetchAll({})
        this.categories = data
      } catch (e) {
        console.log(e)
      } finally {
        this.categoryLoading = false
      }
    },
    async submit() {
      const isInvalid = await this.$refs.observer.validate()
      if (this.data.content) {
        this.contentError = false
      }
      if (isInvalid && this.data.content) {
        const data = {
          title: this.data.title,
          position: this.data.position,
          category_id: this.data.category_id,
          cost_min: this.data.is_free ? 0 : this.data.cost_min,
          cost_max: 0,
          closed_at: this.data.closed_at,
          is_online: this.data.is_online,
          content: this.data.content,
          who: this.data.who,
          work_with: this.data.work_with,
          skills: this.data.skills,
          time_line: this.data.time_line,
        }
        // const { data } = await create(d)
        this.$emit('next', data)
      } else if (!this.data.content) {
        this.contentError = 'The project description can not be empty'
      }
    },
    daysToFormat(value) {
      if (value >= 365) {
        return '1 Year'
      } else if (value === 180) {
        return '半年'
      } else if (value > 30 && value % 30 === 0) {
        return parseFloat(value / 30) + ' Months'
      } else if (value < 30 && value % 7 === 0) {
        return parseFloat(value / 7) + ' Weeks'
      } else {
        return value + 'Days'
      }
    },
  },
}
</script>
