export const collapseCategories = (data, temp = []) => {
  data.forEach((item) => {
    if (item.children) {
      temp.push({
        id: item.id,
        name: item.name,
      })
      temp = collapseCategories(item.children, temp)
    } else {
      temp.push({ id: item.id, name: item.name })
    }
  })
  return temp
}
