                loading || noMoreData
                  ? 'tw-bg-gray-100 tw-border-gray-100'
                  : 'border-color-2 bg-orange-2'
              "
              :disabled="loading || noMoreData"
            >
              {{
                noMoreData ? 'no more data' : loading ? 'loading' : 'load more'
              }}<i
