<template>
  <v-row justify="center" no-gutters>
    <v-col cols="12" lg="10">
      <v-row no-gutters class="mt-8">
        <v-col cols="3" class="d-none d-sm-flex">
          <v-btn
            rounded
            color="deep-orange"
            class="white--text"
            to="/projects/create"
            >Post a New Project</v-btn
          >
        </v-col>
        <v-col cols="12" sm="8" md="4" class="ml-auto">
          <v-select
            label="Select Category"
            small-chips
            flat
            v-model="category"
            :items="allCategories"
            chips
            multiple
            dense
            outlined
            rounded
            item-value="id"
            item-text="name"
          >
            <template v-slot:selection="{ item, index }">
              <v-chip v-if="index === 0" small>
                <span>{{ item.name }}</span>
              </v-chip>
              <span v-if="index === 1" class="grey--text caption"
                >(+{{ category.length - 1 }} others)</span
              >
            </template>
          </v-select>
        </v-col>
        <v-col cols="12" md="4 " class="ml-auto">
          <v-text-field
            label="Search"
            solo
            dense
            hide-details
            clearable
            prepend-inner-icon="mdi-magnify"
            v-model="title"
          ></v-text-field>
        </v-col>
      </v-row>
      <v-row>
        <v-col class="d-none d-md-block" md="3" xl="2">
          <v-card class="pa-2">
            <v-list nav dense>
              <v-list-item-group>
                <v-list-item
                  v-for="(item, i) in leftCategories"
                  :key="i"
                  @click="changeCategory(item.id)"
                >
                  <v-list-item-icon>
                    <v-icon v-text="item.icon"></v-icon>
                  </v-list-item-icon>
                  <v-list-item-content>
                    <v-list-item-title v-text="item.text"></v-list-item-title>
                  </v-list-item-content>
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-card>
          <v-card class="pa-2 mt-4">
            <v-subheader>Status</v-subheader>
            <v-list nav dense>
              <v-list-item @click="changeStatus('all')">
                <v-list-item-content>
                  <v-list-item-title>All</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
              <v-list-item @click="changeStatus('available')">
                <v-list-item-content>
                  <v-list-item-title>Available</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
              <v-list-item @click="changeStatus('unavailable')">
                <v-list-item-content>
                  <v-list-item-title>Unavailable</v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list>
          </v-card>
        </v-col>
        <v-col md="9" xl="10" class="pa-3 pt-0 pa-md-4 pt-md-0">
          <loading
            v-if="loading || projects.length <= 0"
            :loading="loading"
            no-data="No Projects"
          ></loading>
          <v-row v-else>
            <v-col
              cols="12"
              xl="6"
              v-for="item in projects"
              :key="`project-${item.id}`"
            >
              <project-item
                :item="item"
                @change-category="changeCategory"
              ></project-item>
            </v-col>
          </v-row>
        </v-col>
      </v-row>
      <v-row v-if="pages.last_page > 1">
        <v-col cols="12">
          <v-pagination
            v-model="pages.current_page"
            :length="pages.last_page"
          ></v-pagination>
        </v-col>
      </v-row>
    </v-col>
  </v-row>
</template>
<script>
import { fetchIndex } from '@/api/project'
import ProjectItem from '@/components/Project/Item'
import Loading from '@/components/Loading'
import { debounce } from 'lodash'
import { collapseCategories } from '@/utils'
const icons = {
  1: 'desktop-mac',
  7: 'image-filter-vintage',
  13: 'desktop-tower-monitor',
  21: 'dots-vertical',
  19: 'sort-alphabetical-variant',
  28: 'translate',
  39: 'robot-vacuum',
  53: 'rhombus-split',
  67: 'movie-filter',
}
export default {
  components: {
    ProjectItem,
    Loading,
  },
  data() {
    return {
      items: ['Foo', 'Bar', 'Fizz', 'Buzz'],
      items2: [
        {
          icon: 'mdi-inbox',
          text: 'Inbox',
        },
        {
          icon: 'mdi-star',
          text: 'Star',
        },
        {
          icon: 'mdi-send',
          text: 'Send',
        },
        {
          icon: 'mdi-email-open',
          text: 'Drafts',
        },
      ],
      projects: [],
      categories: [],
      model: null,
      pages: {
        last_page: 1,
        current_page: 1,
      },
      category: [],
      loading: false,
      title: null,
      status: 'all',
    }
  },
  mounted() {
    this.loadIndex()
    this.debounceLoadIndex = debounce(this.loadIndex, 800)
  },
  computed: {
    allCategories() {
      return collapseCategories(this.categories)
    },
    leftCategories() {
      return this.categories.map((item) => {
        return {
          icon: 'mdi-' + icons[item.id],
          text: item.name,
          id: item.id,
        }
      })
    },
  },
  watch: {
    category() {
      this.debounceLoadIndex(true)
    },
    title() {
      this.debounceLoadIndex(true)
    },
    status() {
      this.loadIndex(true)
    },
    'pages.current_page'() {
      this.loadIndex()
    },
  },
  methods: {
    async loadIndex(reset = false) {
      this.loading = true
      try {
        const requestParams = {}
        if (this.category) {
          requestParams.category_id = this.category
        }
        if (reset) {
          this.pages = {
            current_page: 1,
            last_page: 1,
          }
          this.projects = []
        }
        if (this.title) {
          requestParams.title = this.title
        }
        if (this.status !== 'all') {
          requestParams.status = this.status
        }
        requestParams.page = this.pages.current_page
        requestParams.includes = 'summary'
        const { data, meta, categories } = await fetchIndex(requestParams)
        this.projects = data
        this.categories = categories
        this.pages = meta
      } catch (e) {
        this.$store.dispatch('message/pushMessage', {
          content: e.response.data.message,
          type: 'error',
        })
      } finally {
        this.loading = false
      }
    },
    changeCategory(id) {
      this.category = [id]
    },
    changeStatus(state) {
      this.status = state
    },
  },
}
</script>
