const state = {
  edit: null,
  saveLoading: false,
  redirectTo: null,
}

const mutations = {
  SET(state, field) {
    state.edit = field
  },
  LOADING(state, status) {
    state.loading = status
  },
  SET_REDIRECT_TO(state, fullPath) {
    state.redirectTo = fullPath
  },
}

const actions = {}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
}
