<template>
  <v-row justify="center">
    <v-col cols="12" md="10" lg="8">
      <v-card>
        <v-card-title>
          Apply Project
        </v-card-title>

        <v-card-text>
          <v-form ref="form" v-model="form">
            <v-row>
              <v-col cols="12" v-if="data.questions">
                <template v-for="(q, index) in answer">
                  <v-textarea
                    :label="`${q.question}`"
                    :key="`question-${index}`"
                    :prefix="`*`"
                    :rules="[rules.requiredAnswer]"
                    v-model="q.content"
                    rows="1"
                  >
                  </v-textarea>
                </template>
              </v-col>
              <v-col cols="12" v-if="data.cost_max > 0">
                <v-text-field
                  v-model="cost"
                  label="Cost"
                  :rules="[
                    rules.required,
                    rules.cost(data.cost_min, data.cost_max),
                  ]"
                  style="max-width: 320px;"
                  :hint="`Must be between $${data.cost_min} - $${data.cost_max}`"
                >
                  <v-icon slot="prepend" color="red">mdi-currency-usd</v-icon>
                </v-text-field>
              </v-col>
            </v-row>
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-btn
            :disabled="!form || isLoading"
            :loading="isLoading"
            @click="apply"
            >Apply</v-btn
          >
          <v-spacer></v-spacer>
          <v-btn @click="$router.go(-1)">Back</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>
<script>
import { apply as applyProject, fetchData } from '@/api/project'
import { get } from 'lodash'
export default {
  computed: {
    id() {
      return this.$route.params.id || 0
    },
  },
  watch: {
    'data.questions'(questions) {
      if (Array.isArray(questions)) {
        questions.forEach((item, index) => {
          this.$set(this.answer, index, {
            question: item.title,
            required: item.checked,
            content: '',
          })
        })
      }
    },
  },
  data: () => ({
    form: false,
    isLoading: false,
    cost: 0,
    answer: [],
    rules: {
      required: (v) => !!v || 'This field is required',
      requiredAnswer: (v) => !!v || 'Please answer the questions',
      cost: (min, max) => (value) =>
        (value >= parseFloat(min) && value <= parseFloat(max)) ||
        `Must be $${min} - $${max}`,
    },
    data: {
      questions: [],
      id: 0,
    },
  }),
  async mounted() {
    const data = this.$store.state.project.data

    if (get(data, 'id', -1) === this.id) {
      this.data = this.$store.state.project.data
    } else {
      await this.loadData()
    }
  },
  methods: {
    async loadData() {
      const { data } = await fetchData(this.id)
      this.data = data
    },
    async apply() {
      this.isLoading = true
      try {
        const res = await applyProject(this.data.id, {
          cost: this.cost,
          answer: this.answer,
        })
        if (res.message) {
          this.$store.dispatch('message/pushMessage', {
            type: 'info',
            content: res.message,
            timeout: 3000,
          })
          this.$router.push(`/projects/${this.id}`)
        }
        this.isLoading = false
      } catch (e) {
        this.isLoading = false
      }
    },
  },
}
</script>
