            text
            @click="$router.push({ name: 'userSettings' })"
            :loading="isApplyLoading"
          >
            Settings
          </v-btn>
          <v-btn
            v-else
            color="primary"
            text
            @click="apply"
            :loading="isApplyLoading"
          >
            Confirm
          </v-btn>
          <v-btn color="primary" text @click="applyDialog = false">
            Cancel
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!--完成任务-->
    <v-dialog v-model="finishedDialog" max-width="500" v-if="isLoggedIn">
